.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.body {
  position: relative;

  display: flex;
  flex-direction: column;

  margin: 0 auto;
  padding: 0;

  min-height: 100vh;
  min-width: 320px;
  box-sizing: border-box;

  font-family: "Montserrat", "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: $color-black;

  background-color: $color-white;
}

.content-wrapper {
  margin: 0 auto;

  max-width: 1160px;
}
